@import "utils/vars";

.result {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 140px 0 250px;

  &__title {
    font-weight: 900;
    font-size: 48px;
    line-height: 100%;
    text-align: center;
    color: #253757;
  }

  &__text {
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    text-align: center;
    padding: 55px 0;
  }

  &__button {
    background-color: $c-button;
    color: $c-text-white;
  }

  .elipse {
    position: absolute;
    border-radius: 50px;
    transform: rotate(34.9deg);
    z-index: 1;
    transition: all 0.3s ease-in-out;

    &--big {
      height: 143px;
      width: 57px;
      border-width: 2px;
    }

    &--medium {
      height: 85px;
      width: 34px;
      border-width: 1.5px;
    }

    &--small {
      height: 45px;
      width: 18px;
      border-width: 1px;
    }

    &--green {
      border: solid #03d542;

      &:hover {
        background-color: #03d542;
      }
    }

    &--blue {
      border: solid #56ccf2;

      &:hover {
        background-color: #56ccf2;
      }
    }

    &--yellow {
      border: solid #fcc304;

      &:hover {
        background-color: #fcc304;
      }
    }

    &--red {
      border: solid #bc04fc;

      &:hover {
        background-color: #bc04fc;
      }
    }

    &--one {
      top: 300px;
      left: 70px;
    }

    &--two {
      top: 80px;
      right: 50px;
    }

    &--three {
      top: 400px;
      right: 10px;
    }

    &--four {
      top: 10px;
      left: 50px;
    }

    &--five {
      top: 550px;
      left: 0;
    }

    &--six {
      top: 350px;
      right: 180px;
    }

    &--seven {
      top: 200px;
      right: -50px;
    }

    &--eight {
      top: 0;
      left: 250px;
    }

    &--nine {
      top: 470px;
      left: 270px;
    }

    &--ten {
      top: 0;
      right: -40px;
    }

    &--eleven {
      top: 550px;
      right: 400px;
    }

    &--twelve {
      top: 380px;
      left: -50px;
    }
  }
}
